.ExternalMenu{
    position: sticky;
    background-color: #8EFE00;
    top:0;
    display:flex;
    align-items: center;
    margin:0;
    justify-content:space-between;
    width: 100vw;
    height: 14vh;
    z-index: 1;
}
.InternalMenu{
    display:flex;
    top:0;
    position: absolute;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: #8EFE00;
    width: 100vw;
    height: 100vh;
    z-index: 2;
}
.waveMenu{
    position: absolute;
    top:0;
    z-index: 3;
    padding:0;
    margin: 0;
    width: 100vw;
    height: 30vh;
}
@media (min-height:600px) and (min-width:600px) {
    .ExternalMenu{
        height: 15vh;
    }
    h1{
        font-family: 'Orbitron', sans-serif;
        padding: 0 2rem 0 2rem;
        font-size: 3rem;
    }
}
@media (min-width:800px) {
    .InternalMenu{
        display:flex;
        top:0;
        height: 15vh;
        position: sticky;
        flex-direction: row;
        align-items: center;
        justify-content:space-between;
        text-align: center;
        background-color: #8EFE00;
        z-index: 1;
    }
    .itens{
        display: flex;
        align-items: center;
    }
    .Ul{
        padding:0;
        display: flex;
        flex-direction: row;
        align-items:flex-end;
        /* justify-content: space-between; */
    }
    .waveMenu{
        display:none;
    }
}
